import React from "react"
import { HEADER_HEIGHT } from "@rp/common/settings"
import { BigdataLogoRegistered } from "@rp/react/common/assets"
import { styled } from "@rp/react/theme"
import { MaxWidthContainer } from "@rp/react/web/common/composite-components"
import { Box, Grid } from "@rp/react/web/mui/core"

type Props = {
  hideUserMenu?: boolean
  leftContent?: React.ReactElement | null
  rightContent?: React.ReactElement | Array<React.ReactElement> | null
  className?: string
  leftColumns?: number
  rightColumns?: number
  showBorder?: boolean
  showLogo?: boolean
}

const BaseHeader = ({
  leftContent = null,
  rightContent = null,
  leftColumns,
  rightColumns,
  className,
  showLogo,
}: Props) => {
  return (
    <header className={className}>
      <MaxWidthContainer sx={{ overflow: "visible" }}>
        {showLogo && (
          <IconContainer>
            <BigdataLogoRegistered width={130} height={34} />
          </IconContainer>
        )}
        <Grid container columnSpacing={3} alignItems="center">
          <Grid item xs={leftColumns || 7}>
            {leftContent}
          </Grid>
          <Grid
            item
            xs={rightColumns || 5}
            container
            justifyContent="right"
            alignItems="center"
            gap={1.5}
            flexWrap="nowrap"
          >
            {rightContent}
          </Grid>
        </Grid>
      </MaxWidthContainer>
    </header>
  )
}

const IconContainer = styled(Box)({
  position: "absolute",
  left: 0,
  width: 200,
  height: "100%",
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
})

const Header = styled(BaseHeader)(({ theme }) => ({
  position: "sticky",
  top: 0,
  zIndex: 12,
  width: "100%",
  display: "flex",
  justifyContent: "center",
  backgroundColor: theme.palette.surface.main,
  borderBottom: `1px solid ${theme.palette.outline.sidebar}`,
  height: HEADER_HEIGHT + 1,
  flexShrink: 0,
  "& > div": {
    display: "flex",
  },
}))

export { BaseHeader, Header }
